import React, { useLayoutEffect } from "react";
import { Route } from "react-router-dom";
import Article from "./article";
import Main from "./main";
import "../css/article.scss";

function ReactPage() {
  useLayoutEffect(() => {
    document.querySelector(".nav-c")?.classList.add("active");

    return () => {
      document.querySelector(".nav-c")?.classList.remove("active");
    };
  });
  return (
    <main>
      <aside>
        <h2>글 목록</h2>
        <ul>
          <li>1</li>
          <li>2</li>
          <li>3</li>
          <li>4</li>
          <li>5</li>
          <li>6</li>
          <li>7</li>
        </ul>
      </aside>
      <article>
        <Route path="/react/" exact component={Main} />
        <Route path="/react/:id/" exact component={Article} />
      </article>
      <div className="margin"></div>
    </main>
  );
}

export default ReactPage;
